<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="createf">
          <p>○ {{$t('code.p1')}}</p>
          <p>○ {{$t('code.p2')}}</p>
        </div>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" v-model="codedata.name" :show-clear-icon="false" :show-success-icon="false" max="10" :placeholder="$t('code.name')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="password" v-model="codedata.password" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('code.pwd')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="password" v-model="codedata.repassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('code.reptpwd')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" v-model="codedata.parentkey" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('code.ycode')"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <yd-flexbox class="reda">
            <div class="chk">
              <yd-checkbox v-model="codedata.check" shape="circle" color="#278eed" size="18">&nbsp;</yd-checkbox>
            </div>
            <yd-flexbox-item>{{$t('code.p3')}} <span @click="privacy()">{{$t('code.p4')}}</span></yd-flexbox-item>
        </yd-flexbox>

        <button type="button" class="btn_c" 
        :class="{'btn_cbj':codedata.name && codedata.password && codedata.repassword && codedata.check}" 
        @click="codedata.name && codedata.password && codedata.repassword && codedata.check && gobeifencode()">{{$t('header.createcode')}}</button>
        <button type="button" class="btn_d" @click="goimportcode()">{{$t('header.importcode')}}</button>
    </div>
  
</template>

<script>
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import { createAccountFun } from '../assets/js/Keytoken.js'
  import { bindAndriodToken } from '@/api/android'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      codedata:{
        name:'',
        password:'',
        repassword:'',
        parentkey:'',
        check:true
      },
      headerNavData:{
         left:true,
         title:this.$t('header.createcode'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      phoneToken:''
    }
  },
  created(){
      if(window.phoneToken){
          this.phoneToken = window.phoneToken.tokenId()
      }
      var del = this.$route.query.del
      if (del) {
        this.headerNavData.left=false
      }
  },
  methods:{
    //跳转导入钱包
    goimportcode(){
      if (this.$route.query.address) {
        this.$router.push({path:'/importcodes',query:{address:this.$route.query.address}})
      }else{
        this.$router.push({path:'/importcodes'})
      }
    },
    privacy(){
      this.$router.push({path:'/writezjc'})
    },
    //创建钱包
    gobeifencode(){
      var that = this
      var name = this.codedata.name
      var password = this.codedata.password
      var repassword = this.codedata.repassword
      var parentkey = this.codedata.parentkey
      var phoneToken = this.phoneToken
      // var pat= /^[a-zA-Z0-9\u4E00-\u9FA5_-]{4,10}$/;
      // if (name == "" || !pat.test(name)) {this.Toast('请输入长度为4-10的名称 不能包含非法字符'); return}
      if (password == "") {this.Toast(this.$t('data.fillpassword')); return}
      if (repassword == "") {this.Toast(this.$t('data.fillduplicatepassword')); return}
      if (password != repassword) {this.Toast(this.$t('data.Inconsistentpassword')); return}
      that.openloading(this.$t('data.Walletcreation'))
      createAccountFun(password,function(address){
        var publickey = "0x" + address.address
        var keystore = JSON.stringify(address)
        that.axios({
            url:that.Url + 'Wallet.ashx',
            method:'post',
            params: {req:1,publickey:publickey,nick:name,parentkey:parentkey}
          }).then(function (res) {
              that.closeloading()
              var _list = res.data;
              if (_list.success) {
                //钱包绑定手机id
                if(window.phoneToken){
                  bindAndriodToken(publickey,phoneToken)
                }
                that.setlist(name,publickey,password,keystore,phoneToken)
                that.$router.push({path:'/beifen_key',query:{key:keystore,password:password,publickey:publickey}})
              }else{
                that.Toast(_list.msg)
              }
          })
      })
    },
    setlist(name,publickey,password,keystore,phoneToken){
      var arry = [];
      var src = Math.ceil(Math.random()*10)
      var obj = {name:name,publickey:publickey,src:src,check:false,keystore:keystore,phoneToken:phoneToken}
      arry.push(obj)
      if (getCookie("codelist")) {
        var codelist =  JSON.parse(getCookie("codelist"))
        var newcodelist = [...codelist, ...arry]
        var jsonStr = JSON.stringify(newcodelist);
        setCookie("codelist",jsonStr)
      }else{
        var jsonStr = JSON.stringify(arry);
        setCookie("codelist",jsonStr)
        setCookie("publickey",publickey)
      }

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
}
.yd-checkbox-text{
  display: none;
}
.reda{
  padding: 0 0 0 .28rem;
  font-size: .26rem;
  color: #666;
}
.reda span{
  color: #278eed
}
.btn_c{
  margin: .35rem .28rem .15rem;
  height: .9rem;
  line-height: .9rem;
  text-align: center;
  width: calc(100% - .56rem);
  border: none;
  background: #cdcdcd;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_d{
  margin: 0 .28rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: calc(100% - .56rem);
  border: none;
  font-size: .28rem;
  color: #278eed;
  border-radius: 5px;
}
.btn_cbj{
  background: #278eed!important;
}
.createf{
  background: #f4f4f4;
  padding: .2rem .35rem; 
  font-size: .26rem;
  color: #278eed
}
</style>



